nav {
  background-color: #2980b9;
  padding: 15px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav h1 {
  margin: 0;
  font-size: 26px;
  padding-left: 1%;
  color: white;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  transition: color 0.3s;
}

nav a:hover {
  color: #ecf0f1;
}

.session-timer {
  margin-right: 20px;
}

.logout-btn {
  background-color: #e74c3c;
  color: #fff;
  border: none;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
  border-radius: 10px;
  -webkit-box-shadow: 2px 3px 18px -1px rgba(0,0,0,0.52);
  -moz-box-shadow: 2px 3px 18px -1px rgba(0,0,0,0.52);
  box-shadow: 2px 3px 18px -1px rgba(0,0,0,0.52);
}

.logout-btn:hover {
  background-color: #c0392b;
}