.Allclients{
  width: 90%;
  display: flex;
}

.Allclients h3{
  margin-left: 20px;
}
.Allclients table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

th, td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  border: 1px solid #ddd;
  background-color: #4CAF50;
  color: #fff;
}

tr:hover {
  background-color: #f5f5f5;
}

.action-btn {
  cursor: pointer;
  background-color: #4CAF50;
  color: #fff;
  padding: 8px 12px;
  text-decoration: none;
  border-radius: 4px;
  margin: 2px;
}

.view-btn:hover {
  background-color: #2980b9;
}
.cllient-edit-form {

  margin-top: 5%;
  }